#maintanance {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 6vw
}
#bigLogo {
    width: 40vw;
    height: 13vw;
    object-fit: cover;
}
#tableImg {
    width: 40vw;
    height: 17vw;
}
#maintanance p {
    margin: 10px 0 0 0;
    font-size: 17px;
}
#maintanance h2 {
    margin-top: 5vw;
}
#maintanance h3 {
    margin-top: 5vw;
    width: 55vw;
    text-align: left;
}
.maintext {
    max-width: 900px;
    margin-top: 7vw;
    font-size: 18px;
    text-align: center;
}
#table2Img {
    width: 55vw;
    height: 33vw;
}
#table3Img {
    width: 55vw; height:28vw;
    margin-top: 1vw;
}
.maintTable {
    margin-bottom: 4vw;
    max-width: 55vw;
}
.maintTable tbody tr:nth-child(1) {
    font-weight: 600;
    background-color: rgb(62, 62, 62);
    color: white;
}
.maintTable tbody tr td:nth-child(1) {
    width: 30vw;
}
@media screen and (max-width:750px) {
    #maintanance {
        align-items: center;
    }
    #maintanance * {
        text-align: center !important;
    }
    #tableImg {
        width: 95vw;
        height: 45vw;
    }
    .maintTable {
        max-width: 95vw;
    }
}