#about {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 800px;
    margin: 50px auto 0 auto;
}
#about p {
    font-size:17px ;
    margin: 0 0 15px 0;
}
#about h3, #about h2 {
    margin-bottom: 8px;
}
#aboutLogo {
    width: 28vw;
    height: 9vw;
    object-fit: cover;
}
#fusLogo {
    width: 20vw;
    height: 3vw;
    margin-top: 70px;
}
@media screen and (max-width:750px) {
    #about {
        align-items: center;
    }
    #aboutLogo, #fusLogo {
        width: 50vw;
        height: 16vw;
    }
    #about * {
        text-align: center;
    }
}